/****** Utility Nav Section ******/

.nav-triangles {
  position: absolute;
  /* top: -2.1rem; */
  left: -2.3rem;
  width: 72px;
  height: 72px;
  @apply top-[-2.3rem] sm:top-[-2.1rem] absolute bg-red-700 transform rotate-45 z-10;
}

.nav-triangles::before {
  content: '';
  top: 0.7rem;
  left: 0.8rem;
  width: 47px;
  height: 47px;
  @apply absolute bg-red-800 z-20;
}

.nav-triangles::after {
  content: '';
  top: 1.2rem;
  left: 1.4rem;
  width: 27px;
  height: 27px;
  @apply absolute bg-red-900 z-30;
}

.util-nav--list > li:not(:last-of-type) a::after {
  content: '';
  left: unset;
  right: 0;
  width: 1px;
  @apply absolute top-1/2 transform -translate-y-1/2 h-sm bg-yellow-400;
}

/* Classes with __ are generated by drupal and PHP */

/* * search input in utils nav */
.search__search-form {
  @apply h-full;
}

.search__search-form form {
  @apply flex justify-end py-sm pl-md pr-md sm:pr-0 m-0;
}

.search__search-form form .form-item {
  @apply w-full sm:w-[16.625rem] mb-0;
}

.search__search-form .search__search-input {
  /* width: 15.625rem; */
  @apply w-full sm:w-[15.625rem] h-full text-sm border-none rounded-sm bg-red-100 pl-md m-0 shadow-none;
}

.search__search-form .search__search-button {
  @apply h-full py-0 px-xs bg-transparent text-white shadow-none;
}

/* * search input om 404 page */
#block-searchform--2 form,
#block-searchform--3 form {
  @apply justify-start w-full pl-0;
}

#block-searchform--2 .input--label,
#block-searchform--3 .input--label {
  @apply block w-full h-full top-[-70%];
  clip: initial;
}

#block-searchform--2 .input,
#block-searchform--3 .input {
  @apply w-full;
}

#block-searchform--2 .search__search-input,
#block-searchform--3 .search__search-input {
  @apply w-full bg-warm-grey-50 shadow-light-1;
}

#block-searchform--2 .search__search-button,
#block-searchform--3 .search__search-button {
  @apply block w-2xl bg-red-500 shadow-light-1;
}

/****** main nav-section ******/

header {
  --nav-height: calc(100vh - 122px);
}

.main-nav .logo {
  max-width: 12.5rem;
}

@media (min-width: 640px) {
  .main-nav .logo {
    max-width: 15.625rem;
  }
}

@media (min-width: 768px) {
  .main-nav nav {
    height: auto;
  }
}

.main-nav--item:nth-last-child(1) .main-nav--dropdown,
.main-nav--item:nth-last-child(2) .main-nav--dropdown {
  left: unset;
  @apply right-0;
}

@media (min-width: 768px) {
  .main-nav--dropdown {
    width: 500px;
  }

  .main-nav--dropdown::after {
    content: '';
    @apply absolute w-[2px] h-full bg-warm-grey-50 top-0 left-1/2 transform translate-x-1/2;
  }
}

.main-nav--dropdown > .dropdown--item > a::after {
  content: '\f054';
  font-family: 'Fontawesome';
  @apply absolute -left-md top-1/2 h-auto text-white md:text-red-500 bg-transparent transition-all duration-300 transform -translate-y-1/2;
}

.main-nav--dropdown > .dropdown--item:hover > a::after,
.main-nav--dropdown > .dropdown--item:focus-within > a::after,
.main-nav--dropdown > .dropdown--item > .is-active::after {
  @apply left-0;
}

@media (min-width: 768px) {
  /* !js adds and removes js-active and slides element on/off screen */
  .second-nav--item .main-nav--third-list {
    @apply left-[-9999px];
  }

  .second-nav--item.js-active .main-nav--third-list,
  .second-nav--item:focus-within .main-nav--third-list {
    @apply left-1/2;
  }
}

.js-mobile-menu {
  height: var(--nav-height);
  top: var(--header-height);
}

@media (min-width: 768px) {
  .js-mobile-menu {
    height: auto;
    top: 0;
  }
}

@media (min-width: 768px) {
  .main-nav--item:hover > .main-nav--dropdown,
  .main-nav--item:focus-within > .main-nav--dropdown,
  .main-nav--item:focus > .main-nav--dropdown {
    @apply block;
  }

  /* .main-nav--dropdown:hover,
  .main-nav--dropdown:focus-within {
    @apply block;
  } */

  .dropdown--item:focus > .main-nav--third-list,
  .dropdown--item:focus-within > .main-nav--third-list,
  .dropdown--item:hover > .main-nav--third-list {
    @apply block;
  }

  .main-nav--link.is-active {
    @apply bg-red-500 text-white;
  }
}
