/* * Should be in accordion */
.show--item:not(last-of-type) {
  @apply mb-md;
}

.show--item:hover {
  @apply shadow-light-3;
}

.show--title .icon::before {
  @apply absolute top-1/2 left-1/2 transform -translate-y-1/2 -translate-x-1/2;
  font-family: 'FontAwesome';
  content: '\f078';
}

.js-open .show--title .icon {
  transform: rotateZ(180deg);
}
